
.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 2vmin);
}

@media (max-width:768px){
  body{
    background-image: url("https://github.com/drnikool/assets/blob/main/hbdmob.jpg?raw=true");
    background-repeat:no-repeat; 
    background-size: cover;
    background-position: center;
    color: #000000;  
    line-height: 100%;
    margin-top: 50%;
    text-align: center;
    font-family: "Monaco", monospace;
  };
}

@media (min-width:769px){
  body{
    background-image: url("https://github.com/drnikool/assets/blob/main/hbd.jpg?raw=true");
    background-repeat:no-repeat;   
    background-size: cover;
    background-position: center; 
    color: #de392d;
    margin-top: 25%; 
    line-height: 100%;
    font-family: "Monaco", monospace;
  };
}

.App-link {
  color: #cbdddc;
}